.App{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background: url('./img/vbg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}
.App>#h1{
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 6px 6px 4px rgba(0, 0, 0, 0.5);
  position: relative;
  bottom: 50px;
  
}
.qr{
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.qr>input,select{
  padding: 10px;
  background: #333;
  color: #fff;
  border-radius: 8px;
  width: 16rem;
}
.qr>input::placeholder{
  color: #fff;
}
.qr2{
  .qr-code {
    border: 2px solid #333;
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
  }
}
#btn{
  border-radius: 8px;
  padding: 10px;
  background-color: pink;
  color: black;
  font-size: 1rem;
  font-weight: 600;
}